.container {
    display: flex;
    padding: 100px 12%;
    background-color: #EDF2F8;
  }
  
  .firstChild > div {
    font-size: 40px;
    font-weight: bold;
   text-align: left;
  }
  .emoji > p {
    font-size:28px;
    color: #1A202C;
  }
  .secondChild {
    flex: 1;
    display: flex;
    justify-content: right;
  }
  .text {
    color:#1A202C;
    font-size: 54px;
    margin-top: -5px;
  }
  .secondChild > img {
    display: block;
    height: 380px;
    width: 370px;
    border-color: #594545;
    
  }

  .para > p {
    font-size:17px;
    margin-top: 2px;
    text-align: left;
    color:#1A202C;
  }
 
  .para1 > p {
    font-size:28px;
    /* text-decoration: underline;
    text-decoration-color: #FC7585; */
    color:#1A202C;
  }
  .resumeBtn {
    height: 50px;
    width: 25%;
    background-color: #E4002B;
    cursor: pointer;
    font-size: 17px;
    color: white;
    border: transparent;
    border-radius: 5px;
  }
  .resumeBtn:hover {
    background-color:#FFFFFF;
    color: #E4002B;
    border:1px solid #E4002B;
  }
  
  .iconDiv {
    display: flex;
    gap: 35px;
    margin-top: 10px;
   
  }
  
  a > img {
    height:30px;
    width: 30px;
  }
  
  
  @media only screen and (min-width: 0px) and (max-width: 280px) {
    .container {
      display: block;
    }
    .firstChild > div {
      font-size: 35px;
      font-weight: bold;
      margin-bottom: 10px;
    }
    .secondChild {
      justify-content: center;
    }
    .secondChild > img {
      height: 150px;
      width: 150px;
      margin-top: 50px;
    }
    .resumeBtn {
      height: 20px;
      width: 25%;
      background-color:#E4002B;
      cursor: pointer;
      font-size: 11px;
      border-radius: 3px;
    }
    .resumeBtn:hover {
      background-color:#FFFFFF;
      color: #E4002B;
      border:1px solid #E4002B;
    }
    .text{
      font-size: 36px;
    }
    .emoji > p {
      font-size: 20px;
    }
    .para > p {
      font-size: 13px;
    }
  }
  
  
