.container {
  padding: 70px 0px;
  margin-bottom: 20px;
}

.heading {
  color: #E4002B;
  text-align: center;
  font-size: 40px;
  font-weight: bolder;
  margin-bottom: 20px;
}

.toolContainer {
  margin:50px auto;
  width: fit-content;
  height: fit-content;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-content: center;
  gap: 12%;
}

.toolContainer > div {
  padding: 10px;
  border-radius: 10px;
  background-color: #EDF2F8;
  color: #1A202C;
}

.toolContainer > div > a > img {
  height: 80px;
  width: 160px;
}

.toolContainer > div:hover {
  border: 1px solid red;
  background-color: #FFFFFF;
  box-shadow: 1px 1px 17px 5px rgba(255, 98, 37, 0.48);
  -webkit-box-shadow: 1px 1px 17px 5px rgba(255, 98, 37, 0.48);
  -moz-box-shadow: 1px 1px 17px 5px rgba(255, 98, 37, 0.48);
}
.Github {
  width: 80%;
  margin: auto;
  margin-left: 500px;
}
@media only screen and (min-width: 0px) and (max-width: 280px) {
  .toolContainer {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10%;
  }
  .toolContainer > div > a > img {
    height: 80px;
    width: 80px;
    margin: auto;
  }
  
  .heading {
    margin-top: -50px;
    color:#E4002B;
    font-size: 28px;
  }
  .backend {
    margin-top: 100px;
  }
}

