.toolContainer1 {
  margin: auto;
  width: 85%;
  height: fit-content;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 5%;
}

.toolContainer1 > div {
  width: 100%;
  padding: 25px;
  height: 200px;
  background-color: #0f1624;
  color: #ffffff;
  border-radius: 25px;
}
.toolContainer1 > div > img {
  width: 9vw;
  margin: auto;
}
.toolContainer1 > div > h1 {
  margin-top: 6px;
  font-weight: bolder;
}
.container1 {
  background-color: #edf2f8;
  padding: 50px;
}
.heading {
  text-align: center;
  font-size: 40px;
  color: #e4002b;
  font-weight: bolder;
}
.container1 > h1 {
  text-align: center;
}
.container1 > p {
  text-align: left;
  margin-left: 100px;
  font-size: 28px;
  font-weight: bolder;
  margin-bottom: 10px;
  color: #0f1624;
}
.container2 > p {
  text-align: left;
  margin-left: 100px;
  font-size: 28px;
  font-weight: bolder;
  margin-top: 40px;
  margin-bottom: 10px;
  color: #0f1624;
}
.toolContainer1 > div:hover {
  background-color: #e4002b;
}

@media only screen and (min-width: 0px) and (max-width: 280px) {
  .toolContainer1 {
    margin: auto;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 2%;
  }
  .toolContainer1 > div > img {
    height: 75px;
    width: 75px;
    margin: auto;
  }
  .toolContainer1 > div {
    margin: auto;
    width: 90%;
    padding: 15px;
    height: 150px;
    background-color: #0f1624;
    color: #ffffff;
    border-radius: 18px;
  }
  .toolContainer1 > div > h1 {
    font-weight: bolder;
    font-size: 15px;
  }
  .container1 > p {
    margin-left: 20px;
    font-size: 15px;
    margin-top: 20px;
  }
  .container1 > h1 {
    text-align: center;
    font-size: 25px;
  }
  .container2 > p {
    margin-top: 50px;
    font-size: 15px;
    margin-left: 20px;
  }
}

