.Github {
  width: 70%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 40px;
  margin: auto;
  background-color:#161616;
  color:white;
  padding: 5px;
}
.Github1 {
  width: 90%;
  display: grid;
  grid-template-columns: repeat(2 , 1fr);
  gap:40px;
  margin:60px auto;
}
.Github1 > div  {
  width: 100%;
  margin: auto;
}


@media only screen and (min-width: 0px) and (max-width: 280px) {
    .Github1 > a > img {
        width: 85%;
        height: 200px;
        margin:auto;
      }
      .Github {
        width: 95%;
      }
      .Github1 {
        width: 90%;
        display: grid;
        grid-template-columns: repeat(1 , 1fr);
        gap:40px;
        margin:60px auto;
      }
      .Github1 > div  {
        width: 100%;
        margin: auto;
      }
}
