*{
  margin: 0px;
  padding: 0px;
}
.container {
    padding: 70px 25%;
  
  }
  .slide {
    width: 75%;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap:15px;
    margin: auto;
  }
  .slide > div {
   font-size: 22px;
   font-weight: bolder;
   margin: auto;
   border-radius: 10px;
   color: #1A202C;
   background-color: #EDF2F8;
  }
  .slide1 {
    width:100%;
    padding: 16.5px;
  }
  .slide > div:hover {
    border: 1px solid red;
    background-color: #FFFFFF;
    box-shadow: 1px 1px 17px 5px rgba(255, 98, 37, 0.48);
    -webkit-box-shadow: 1px 1px 17px 5px rgba(255, 98, 37, 0.48);
    -moz-box-shadow: 1px 1px 17px 5px rgba(255, 98, 37, 0.48);
  }
 .pic {
  display: flex;
  flex-direction: row;
  gap: 16vw;
 }
 .pic > img {
    width:50vw;
    height:50vh;
   float: left;
   margin-left: -160px;
   /* border-radius: 40px; */
   border-radius: 100%;
  }
  .heading {
    /* color: #4070f4; */
    color:#DC143C;
    text-align: center;
    margin-bottom: 40px;
    font-size:40px;
    font-weight: bolder;
  }
  
  .heading2 {
    text-align:left;
    font-size:25px;
    font-weight: bolder;
    margin-top: 25px;
    color:#1A202C;
  }
  
  .para {
    width:28vw;
    text-align:justify;
    font-size:20px;
    line-height: 30px;
    word-spacing: -1px;
    margin-top: 10px;
    color:#1A202C;
    font-weight: 620;
  }
  .secondChild {
    flex: 1;
    display: flex;
    justify-content: left;
  }
  .mail {
    margin-top: 20px;
    text-align: left;
    color:#1A202C;
    font-weight: 500;
  }
  
